  z-index: 1;
  overflow: hidden;
  width: 100%;
  padding: 20px 0; }
  .page__body {
    width: 100%;
    height: auto;
    background-color: #1a1a1a;
    color: #fff;
    padding-top: 110px;
    padding-left: 20px;
    padding-right: 20px;
    transition: 0.3s; }
    .page__body:before, .page__body:after {
      content: '';
      position: absolute;
      top: 0;
      width: 20px;
      height: 100%;
      background-color: #f5f5f5;
      z-index: 1; }
    .page__body:before {
